.post {
  box-sizing: border-box;
  width: 100%;
  padding: 20px;
  background-color: #fff;
  border-radius: 5px;

  &__header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.1em;

    &:hover {
      cursor: pointer;
    }

    & :nth-child(n + 2) {
      margin-left: 20px;
    }
  }

  &__data {
    display: flex;
    width: 50%;
    align-items: center;
  }

  &__content {
    border: 1px solid rgb(0, 153, 255);
    border-radius: 5px;
    padding: 5px;
    margin-top: 20px;
    font-size: 2em;

    &:hover {
      cursor: pointer;
    }
  }

  &__save {
    display: block;
    margin-top: 15px;
    margin-left: auto;
    margin-right: 0;
  }
}